import { getComments } from '@/api/article'
import { mapState } from 'vuex'
export default {
  name: 'ArticleComment',
  props: {
    article: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      comments: [], //文章列表
    }
  },
  computed: {
    ...mapState(['user'])
  },

  async mounted() {
    const { data } = await getComments(this.article.slug)
    this.comments = data.comments
  },
}
